@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;500&family=Mulish:wght@700;800&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body {
  background-color: #F9FAFD;
}

.App {
  text-align: center;
  background-color: #F9FAFD;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

h1 {
  font-family: 'Mulish', sans-serif;
  font-weight: 800;
  font-size: 28pt;
  letter-spacing: 0.5pt;
  color: #092E50;
}

.About {
  position: relative;
  display: block;
  margin: 5px;
  max-width: 1080px;
  top: 50px;
  align-self: center;
  margin-bottom: 100px;
  font-size: calc(10px + 2vmin);
}

.shape {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 1000px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 50;
  top: -10% !important;
}



.row {
  position: relative;
  margin: 5px;
  max-width: 1080px;
  display: block;
  align-self: center;
  display: flex;
}

.Experiences {
  float: left;
  width: 800px;
  margin: 15px;
  padding: 5;
  text-align: left;
}

.Projects {
  text-align: left;
  margin-right: 0px;
  margin-top: 15px;
  float: right;
  width: 300;
  padding: 5;
}

@media screen and (max-width: 600px) {
  .Projects
  .Experiences {
    width: 100%;
  }
}

.path1, .path2 {
  position: fixed;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 2;
}

.path1 {
  right: 0;
  width: 96%;
  top: -90%;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash1 5s linear forwards;
          animation: dash1 5s linear forwards;
}

.path2 {
  stroke-dashoffset: 1000;
  stroke-dasharray: 1000;
  -webkit-animation: dash2 10s linear forwards;
          animation: dash2 10s linear forwards;
  left: -35%;
  bottom: -50%;
}

@-webkit-keyframes dash1 {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash1 {
  to {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes dash2 {
  
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash2 {
  
  to {
    stroke-dashoffset: 0;
  }
}

.bg-shape {
  position: absolute;
  top: 0;
  overflow: hidden;
  display: flex;
}

.App {
-webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 2s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */

/* Opera < 12.1 */

  .Projects, .Experiences {
  -webkit-animation: fadein 4s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 4s;
  z-index: 1;
}
.image {
    animation: fadeIn ease 20s;
    -webkit-animation: fadeIn ease 20s;
    -moz-animation: fadeIn ease 20s;
    -o-animation: fadeIn ease 20s;
    -ms-animation: fadeIn ease 20s;
  
    border-radius: 10px;
    width: 400px;
    height: 400px;
    transition:         transform .8s ease-in-out;
  } 

.image:hover {
    transform: rotate(20deg);
  }


a:link {
    color: #507ed3;
    text-decoration: none;
    border-bottom: 0px solid #C4E7D6;
    box-shadow: 0 -6px 0 #C4E7D6 inset;
}
a:hover {
    color: #507ed3;
    text-decoration: none;
    border-bottom: 0px solid #d1f1e1;
    box-shadow: 0 -6px 0 #d1f1e1 inset;
}

a:visited {
    color: #507ed3;
    text-decoration: none;
}

a.clicker:focus {
    /* background-color: #FEC2AF; */
    background-color: #d1f1e1;
    border-bottom: 0px solid #d1f1e1;
    box-shadow: 0 -6px 0 #d1f1e1 inset;
}



.title {
    margin-left: -40px;
    font-family: 'Mulish', sans-serif;
    font-weight: 700;
    font-size: 72pt;
    color: #092E50;
    padding: 0px;
    line-height: 85pt;
    z-index: 1;
    -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 1s;
}

 .About {
    display: flex;
} 

